////
/// Summary Module
/// @group summary
////

#{ns(summary)} {
  @include scheme-element-bg-color(beige, true);

  @include title {
    @include scheme-element-text-color(g800, true);
  }

  li > a {
    @include scheme-element-text-color(g800, true);
    @include scheme-element-bg-color(t-plain, true, after);
  }
}
